<template>
  <div id="app">
    <div v-show="showLoading" class="loading">
      <b-spinner variant="success" label="Spinning"></b-spinner>
    </div>
    <div id="264_AppHeader"><appheader_264></appheader_264></div><div id="271_TheHero"><thehero_271></thehero_271></div><div id="273_HorizontalCards"><horizontalcards_273></horizontalcards_273></div><div id="274_TwoColsImgRigth"><twocolsimgrigth_274></twocolsimgrigth_274></div><div id="275_TwoColsImgLeft"><twocolsimgleft_275></twocolsimgleft_275></div><div id="279_Gallery"><gallery_279></gallery_279></div><div id="282_ContactUs"><contactus_282></contactus_282></div><div id="285_Footer"><footer_285></footer_285></div>
    <book-consultation @loaded="loaded" @opened="opened"></book-consultation>
  </div>
</template>

<script>
  
  import Vue from "vue";

  import appheader_264 from './components/appheader_264';
import thehero_271 from './components/thehero_271';
import horizontalcards_273 from './components/horizontalcards_273';
import twocolsimgrigth_274 from './components/twocolsimgrigth_274';
import twocolsimgleft_275 from './components/twocolsimgleft_275';
import gallery_279 from './components/gallery_279';
import contactus_282 from './components/contactus_282';
import footer_285 from './components/footer_285';


  Vue.prototype.$restRoute = '/vlox/assets/components/vlox';

  export default {
    name: 'App',
    components: { appheader_264,
thehero_271,
horizontalcards_273,
twocolsimgrigth_274,
twocolsimgleft_275,
gallery_279,
contactus_282,
footer_285,
},
    
        mounted() {
            //wait for the tawk api to load
            const interval = setInterval(function() {
              if (window.$_Tawk && window.$_Tawk.downloaded && window.$_Tawk.showWidget instanceof Function) {
                window.$_Tawk.showWidget()
                clearInterval(interval);
              }
            }, 500);
        
          },
    
    data(){
      return{
        showLoading: false
      }
    },
    methods: {
      loaded(){
        this.showLoading = false;
      },
      opened() {
        this.showLoading = true;
        setTimeout(() => {
          this.showLoading = false;
        }, 5000);
      }
    }
  }
</script>

